@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
*,*::before,*::after{box-sizing:border-box}body{font-family:"Roboto",sans-serif;margin:0;padding:0;overflow-y:scroll}


.cartscreen{display:flex;max-width:1300px;margin:2rem auto}.cartscreen h2{margin-bottom:1rem}.cartscreen__left{flex:0.7 1;margin-right:1rem;background:transparent;padding:1rem}.cartscreen__right{flex:0.3 1;background:#fff;box-shadow:0 1px 4px rgba(0,0,0,0.4);height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.cartscreen__right div{border-bottom:1px solid rgba(0,0,0,0.2);padding:1rem}.cartscreen__info p{padding:8px}.cartscreen__info p:first-child{font-weight:bold}.cartscreen__right div:last-child{border:none}.cartscreen__right div button{padding:10px 17px;width:100%;background:#171717;color:#f4f4f4;border:1px solid #171717;cursor:pointer}.cartscreen__right div button:hover{opacity:0.9}@media (max-width: 1320px){.cartscreen{max-width:900px}}@media (max-width: 960px){.cartscreen{max-width:800px}}@media (max-width: 960px){.cartscreen{flex-direction:column}.cartscreen__right{margin:1rem}}

.CartItem_cartitem__27x5q{width:100%;padding:1rem;display:grid;grid-template-columns:3fr 2fr 1fr 1fr 1fr;grid-gap:8px;gap:8px;background:#fff;border-radius:2px;place-items:center;margin-bottom:8px}img{height:50%;width:50%}.CartItem_cartItem__name__tzNrf{color:#171717}.CartItem_cartItem__name__tzNrf:hover{color:#f50057}.CartItem_cartItem__select__1-9Zo{padding:5px 5px}.CartItem_cartItem__deleteBtn__2WELD{padding:5px 17px;color:#f50057;background:#f4f4f4;border:none;cursor:pointer;transition:all 0.3s ease-out}.CartItem_cartItem__deleteBtn__2WELD:hover,.CartItem_carItem__deleteBtn__3P3jm:active,.CartItem_carItem__deleteBtn__3P3jm:focus{-webkit-transform:scale(1.2);transform:scale(1.2)}@media (max-width: 700px){.CartItem_cartItem__name__tzNrf{font-size:0.8rem}.CartItem_cartItem__select__1-9Zo,.CartItem_cartItem__deleteBtn__2WELD{padding:8px 13px}.CartItem_cartitem__price__1ZVaM{font-size:0.8rem}}@media (max-width: 700px){.CartItem_cartItem__name__tzNrf{font-size:0.6rem}.CartItem_cartItem__select__1-9Zo,.CartItem_cartItem__deleteBtn__2WELD{padding:5px 8px}.CartItem_cartitem__price__1ZVaM{font-size:0.6rem}}

